<template>
	<view class="content_box">
		<Head :title="type==1?'创建NFT':'创建合集'"/>
		 <view class="content relative">
			 <!-- 形象标识 -->
			 <view class="topimgUp" v-if="type==2">
			 	 <view class="title flex">
			 	 	<text>形象标识</text> <image src="../../static/img/imgbs.png" mode="widthFix" style="width: 14rpx;"></image>
			 	 </view>
				<view class="imgbox relative">
					<u-upload
							:fileList="fileList"
							@afterRead="afterRead"
							@delete="deletePic"
							name="1"
							multiple
							:maxCount="10"
						>
						<view class="flexC abcenter absolute textAc">
							<image src="/static/img/2552.png" class="image255"></image>
							<text class="fontS28">上传图像、视频</text>
						</view>
					</u-upload>
				</view>
			</view>
			<!-- 特色图片 -->
			<view class="lis">
				<view class="title">特色图片</view>
				<view class="upImg relative ">
					<u-upload
						:fileList="fileList"
						@afterRead="afterRead"
						@delete="deletePic"
						name="6"
						multiple
						:maxCount="1"
						width="100%"
						height="400rpx"
					>
						<view class=" flexC abcenter absolute textAc">
							<image src="/static/img/2552.png" class="image255"></image>
							<text class="fontS28">上传图像、视频</text>
						</view>
					</u-upload>
				</view>
				<view class="fontS20 font99">
					支持的文件类型：JPG、SVG、png
				</view>
			</view>
			<!-- 横幅banner -->
			<view class="lis" v-if="type==2">
				<view class="title">横幅banner</view>
				<view class="upImg banner relative " >
					<u-upload
						:fileList="fileList"
						@afterRead="afterRead"
						@delete="deletePic"
						name="6"
						multiple
						:maxCount="1"
						width="100%"
						height="243rpx"
					>
						<view class=" flexC abcenter  absolute textAc">
							<image src="/static/img/2552.png" class="image255"></image>
							<text class="fontS28">上传图像、视频</text>
						</view>
					</u-upload>
				</view>
			</view>
			<!-- 标题 -->
			<view class="lis" v-if="type==1">
				<view class="title">
					标题
				</view>
				 <u--input
				    placeholder="请输入藏品名"
				    border="surround"
				    v-model="titleValue"
				    @change="change"
				  ></u--input>
			</view>
			<!-- 集合名称 -->
			<view class="lis" v-if="type==2">
				<view class="title flex">
					<text>集合名称</text> <image src="../../static/img/imgbs.png" mode="widthFix" style="width: 14rpx;"></image>
				</view>
				 <u--input
				    placeholder="请输入集合名称"
				    border="surround"
				    v-model="titleValue"
				    @change="change"
				  ></u--input>
			</view>
			<!-- 简介 -->
			<view class="lis">
				<view class="title">
					简介
				</view>
				<u--textarea v-model="intruValue" placeholder="请输入内容" ></u--textarea>
				 <view class="fontS20 font99">
				 	描述将出现在其图像下方的项目详细信息页面上。
				 </view>
			</view>
			<!-- 选择合集 -->
			<view class="lis" v-for="(item,index) in lis" :key="index">
				<view class="title">
					{{item.title}}
				</view>
				<view class="box">
					<view class="flex flexic" @click="item.show=true">
						<image src='/static/img/aa.jpg'  class='brYuan'></image>
						<view class="picker font33 fontS28" >
							<text v-for="item1 in item.menu">
								 {{item.active==item1.id?item1.name+'':''}}
							</text>
							
						</view>
						<u-icon :name="item.show?'arrow-up':'arrow-down'" color="#333333" size='18'></u-icon>
					</view>
					 <u-overlay :show="item.show"   @click="item.show = false">
						 <view class="warp" @tap.stop> 
							 <view class="fontS36 textAc">
							 	{{item.title}}
							 </view>
							<view class="flexJBC flexW"> 
								<view class="lis flex flexic fontS32 " v-for="(item1) in item.menu" :key="item1.id" :class="item.active==item1.id?'active':''" @click="activeClick(index,item1.id)">
									<image src="" mode="" class="brYuan" v-if="index!=2" ></image><text>{{item1.name}}</text>
								</view>
							</view>
						</view>
					 </u-overlay>
				</view>
			</view> 
			
		    <!-- 创作者收益 -->
			<view class="lis" v-if="type==2">
		    	<view class="title flex">
		    		<text>创作者收益</text> <image src="../../static/img/imgbs.png" mode="widthFix" style="width: 14rpx;"></image>
		    	</view>
		    	<u--input
		    	    style='padding: 0 30rpx;margin-bottom: 28rpx;'
		    	    placeholder="请输入内容"
		    	    border="none"
		    	    v-model="earnings"
		    	    @change="change"
		    	  >
		    	  <template slot="suffix">
		    	  	<view class="">
		    	  		%
		    	  	</view>
		    	  </template>
		    	</u--input>
				<view class="fontS20 font99">
					当用户转售您最初创建的项目时收取费用。从最终售价中扣除，并按月支付到您选择的支付地址。请输入10%-30%。
				</view>
		    </view>
			<!-- 支付钱包地址 -->
			<view class="lis" v-if="type==2">
				<view class="title flex">
					<text>支付钱包地址</text> <image src="../../static/img/imgbs.png" mode="widthFix" style="width: 14rpx;"></image>
				</view>
				<u--input
				    placeholder="请输入地址"
				    border="none"
				    v-model="address"
				    @change="change"
				  >
				</u--input>
				
			</view>
			<!-- 上链费 -->
			<view class=" fontS32" v-if="type==1">
				<view class="title"></view>
		    	<view class="flexJBC">
		    		<text class=" font99">上链费：</text>
		    		<text class="font60">0.027ETH</text>
		    	</view>
		    </view>
			
			<view class="btn00 paybtn " v-if="type!=3">
				{{type==1?'创建并支付':'支付'}}
			</view>
			<view class="btnn flexJBC fixed" v-else>
				<view class="btn00 fontS32" >
					修改
				</view>
				<view class="fontS32 font99" >
					删除
				</view>
			</view>
		 </view>
	</view>
</template>

 <script>
 	export default {
 			data() {
 				return {
				value:'',
				lis:[
					// 合集
					{title:"选择合集",show:false,active:1,menu:[{name:'合集1',id:1},{name:"合集2",id:2}]},
					// 区块链
					{title:"选择区块链",show:false,active:1,menu:[{name:'Solana',id:1},{name:"Ethereum",id:2}]},
					// 类别
					{title:"添加类别",show:false,active:1,menu:[{name:'类别1',id:1},{name:"类别2",id:2}]},
				],
				index:0,
				
				
					type:1,// 1标识当前页面是创建Nft  2标识是创建合集页面  3创建NFT编辑
					address:'',//地址
					earnings:'',//收益
					titleValue:"", //标题
					intruValue:"", //简介
 					fileList: [],
 				}
 			},
			onLoad(e) {
				if(e.type){
					this.type=e.type
				}
			},
 			methods:{
				activeClick(index,id){
					this.lis[index].active=id
				},
				bindPickerChange() {
					
				},
				confirm(e){
					console.log(e)
				},
 				// 删除图片
 				deletePic(event) {
 					this[`fileList${event.name}`].splice(event.index, 1)
 				},
 				// 新增图片
 				async afterRead(event) {
 					// 当设置 mutiple 为 true 时, file 为数组格式，否则为对象格式
 					let lists = [].concat(event.file)
 					let fileListLen = this[`fileList${event.name}`].length
 					lists.map((item) => {
 						this[`fileList${event.name}`].push({
 							...item,
 							status: 'uploading',
 							message: '上传中'
 						})
 					})
 					for (let i = 0; i < lists.length; i++) {
 						const result = await this.uploadFilePromise(lists[i].url)
 						let item = this[`fileList${event.name}`][fileListLen]
 						this[`fileList${event.name}`].splice(fileListLen, 1, Object.assign(item, {
 							status: 'success',
 							message: '',
 							url: result
 						}))
 						fileListLen++
 					}
 				},
 				uploadFilePromise(url) {
 					return new Promise((resolve, reject) => {
 						let a = uni.uploadFile({
 							url: 'http://192.168.2.21:7001/upload', // 仅为示例，非真实的接口地址
 							filePath: url,
 							name: 'file',
 							formData: {
 								user: 'test'
 							},
 							success: (res) => {
 								setTimeout(() => {
 									resolve(res.data.data)
 								}, 1000)
 							}
 						});
 					})
 				},
 			}
 	
 		}
 </script>
 
<style lang="scss" scoped>
	.btnn{
		bottom: 0;
		padding: 30rpx 40rpx;
		box-sizing: border-box;
		width: 100%;
		background-color: #fff;
		left: 0;
		.btn00{
			width: 80%;
			height: 115rpx;
			line-height: 115rpx;
			margin-right: 68rpx;
		}
	}
	.banner{
		height: 243rpx !important;
	}
	.topimgUp{
		.imgbox{
			width: 200rpx;
			height: 200rpx;
			background: #FFFFFF;
			border-radius: 50%;
		}
		
	}
	.image255{
		width: 50rpx;
		height: 44rpx;
		margin:28rpx auto;
	}
	.paybtn{
		margin-top: 78rpx;
		height: 115rpx;
		line-height: 115rpx;
		font-size: 32rpx;
	}
	.warp{
		width: 100%;
		/* height: 466rpx; */
		position: fixed;
		bottom: 0;
		padding: 40rpx 40rpx 70rpx;
		box-sizing: border-box;
		background: #FFFFFF;
		border-radius: 40rpx 40rpx 0px 0px;
		.lis{
			
			justify-content: center;
			width: 319rpx;
			height: 72rpx;
			background: #FFFFFF;
			border: 2rpx solid #0F172C;
			border-radius: 20rpx;
			line-height: 72rpx;
			text-align: center;
			margin-top: 32rpx;
			image{
				width: 48rpx;
				height: 48rpx;
				background-color: #fff;
				border: 1rpx solid #707070;
			}
		}
		.active{
			background-color: #0F172C;
			color: #fff;
		}
	}
	.box{
		image{
			width: 48rpx;
			height: 48rpx;
			margin-right: 20rpx;
		}
		width: 100%;
		height: 106rpx;
		line-height:106rpx;
		padding:0 38rpx;
		box-sizing: border-box;
		border-radius: 32rpx;
		background:#fff;
		.picker{
			width: 500px;
		}
	}
	.title{
		margin: 72rpx 0 40rpx;
		color: #0F172C;
	}
	/deep/.u-input ,/deep/.u-textarea{
		width: 100%;
		background: #FFFFFF;
		border-radius: 32rpx;
		padding-left: 38rpx !important;
		box-sizing: border-box;
	}
	/deep/.u-input{
		height: 106rpx;
	}
	/deep/.u-textarea{
		height: 303rpx;
		margin-bottom: 28rpx;
	}
	.upImg{
			width: 100%;
			border-radius: 40rpx !important;
			background-color: #fff !important;
		    margin: 48rpx 0 28rpx;
		//  /deep/.u-upload__button{
		// 	width: 100%;
			height: 400rpx;
		// 	border-radius: 40rpx !important;
		// 	background-color: #fff !important;
		// }
	}
	.banner{height: 243rpx;}
	.content{
		padding: 0 40rpx;
		box-sizing: border-box;
	}
</style>